<template>
    <div class="card bg-blue-100 flex flex-col border border-gray-400 p-1 mt-5 w-full h-3/4 rounded-md overflow-y-auto">  
     <p>Structural Member Estimate</p>
     <p v-if="message">{{message}}
        <div class="flex flex-row">
            <div class="w-1/2">
                
                <ul class="card flex flex-col bg-blue-200 p-1 rounded-lg">
                    <li>
                        <table class="text-sm text-teal-700 font-semibold">
                            <caption>Project</caption>
                                <tr>
                                    <th></th>
                                    <th></th>
                                </tr>
                                <tr>
                                    <td class="text-xs w-1/4">Project Phase</td>
                                    <td  class="text-xs w-3/4">
                                    <div class="flex flex-row">
                                        <div class="w-1/2 mr-1">
                                        <input class="px-2 py-1 rounded-l-lg border hover:border-gray-500 w-full" type="text" placeholder="Project Phase" v-model="project.estimate.phase">
                                        </div>
                                        <div class="w-1/2 ml-1">

                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700  px-1 py-1  rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="project.estimate.phase">
                                        
                                        <option v-for="p of phases" :key="p">{{p}}</option>
                                        </select>

                                       
                                        </div>

                                    </div>
                                    
                                    </td>
                                </tr>

                                <tr>
                                    <td class="text-xs">Section</td>
                                    <td>
                                    <div class="flex flex-row">
                                        <div class="w-1/2 mr-1">
                                        <input type="text" placeholder="Section" class="px-2 py-1 rounded-l-lg border hover:border-gray-500 w-full" v-model="project.estimate.section">
                                        </div>
                                        <div class="w-1/2 ml-1">

                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-1 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="project.estimate.section">
                                        
                                        <option v-for="s of sections" :key="s">{{s}}</option>
                                        </select>

                                       
                                        </div>

                                    </div>
                                    
                                    </td>
                                </tr>
                        </table>
                    </li>

                      <li>
                        <table class="text-sm text-teal-700 font-semibold border border-gray-400">
                            <caption>Structural Member</caption>
                                <tr>
                                    <th></th>
                                    <th></th>
                                </tr>
                                <tr>
                                    <td class="text-xs"></td>
                                    <td>
                                    <div class="flex flex-row">
                                        
                                        <div class="w-1/2 mr-1">
                                        <label class="text-xs" for="category">Category</label>
                                        <select id="category" class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="beam.category">
                                        
                                        <option v-for="cat of beam_categories" :key="cat.value" :value="cat.value">{{cat.text}}</option>
                                        </select>
                                        </div>

                                         <div class="w-1/2 ml-1" v-if="beam.category == 'concrete'">
                                         <label class="text-xs" for="ratio">Mix-Ratio</label>
                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="beam.mix.ratio">
                                             <option value="" hidden>Ratio</option> 
                                            <option value="1:3:6">1:3:6</option>
                                            <option value="1:3:5">1:3:5</option>
                                            <option value="1:2:4">1:2:4</option>                                          
                                            <option value="1:2:3">1:2:3</option>
                                            <option value="1:2:2">1:2:2</option>
                                            <option value="1:1:1">1:1:1</option>
                                        </select>
                                        </div>
                                    </div>                                    
                                    </td>
                                </tr>

                                <tr>



                                    <td></td>
                                    <td>
                                         <div class="flex flex-row">
                                        
                                        <div class="w-1/2 mr-1">
                                        <label class="text-xs" for="beam-type">Beam Type</label>
                                         

                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-walltype" v-model="beam.beamtype">
                                        
                                        <option v-for="bt of beamtypes" :key="bt.value" :value="bt.value">{{bt.text}}</option>
                                        </select>

                                       
                                        </div>
                                        
                                         <div class="w-1/2 ml-1" >Tag
                                           <input type="text" class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" v-model.lazy="beam.tag">
                                            
                                        </div>
                                         </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>
                                        <div class="flex flex-row">
                                            <div class="w-1/2 mr-1"  @focusout="setInputUnit()">
                                             <span class="text-xs font-semibold ml-2">Unit</span>
                                                <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-walltype" v-model="beam.unit">
                                            
                                                    <option>metric</option>
                                                    <option>imperial</option>
                                                </select>
                                            </div>
                                            <div class="w-1/2 ml-1"> 
                                                 <span class="text-xs font-semibold ml-2">Length</span>
                                                 <span class="text-xs text-red-600 font-semibold ml-2">{{fieldunit}}</span>
                                                <input type="number" class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" v-model.number="beam.length">
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                
                                 <tr>
                                    <td class="text-xs"></td>
                                    <td>
                                        <div class="flex flex-row">
                                             
                                            <div class="w-1/2 mr-1">
                                             <span class="text-xs font-semibold ml-2">Width</span>
                                              <span class="text-xs text-red-600 font-semibold ml-2">{{inputunit}}</span>
                                            <input type="number" class="px-2 py-1 w-full rounded-l-lg border hover:border-gray-500" v-model.number="beam.bredth">
                                            
                                            </div>
                                            <div class="w-1/2 ml-1">
                                             <span class="text-xs font-semibold ml-2">Depth</span>
                                              <span class="text-xs text-red-600 font-semibold ml-2">{{inputunit}}</span>

                                            <input type="number" class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" v-model.number="beam.depth">
                                            </div>
                                        </div>
                                        
                                      
                                    </td>
                                    
                                </tr>
                                
                                
                        </table>
                    </li>

                     <li v-if="beam.category == 'concrete'">
                        <table class="text-sm text-teal-700 font-semibold border border-gray-400">
                            <caption>Reinforcement</caption>
                                <tr>
                                    <th><div class="flex flex-row items-center"><span class="mx-2">Spanned</span><input type="checkbox" :value="beam.rebar.stirrup.span" v-model="beam.rebar.stirrup.span"></div></th>
                                    <th></th>
                                </tr>
                                 <tr>
                                    <td class="text-xs"></td>
                                    <td>
                                    <div class="flex flex-row">
                                      
                                        <div class="w-1/2 mr-1">
                                         <label class="text-xs" for="topbar-type">Topbar Type</label>
                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2 rounded-l-lg leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="beam.rebar.top.bartype">
                                        
                                        <option v-for="bar of bartypes" :key="bar.value" :value="bar.value">{{bar.text}}</option>
                                        </select>
                                        </div>
                                        <div class="w-1/2 ml-1">
                                             <label class="text-xs" for="ratio">Topbar Amt.</label>
                                          <input class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" placeholder="Amt" type="text" v-model="beam.rebar.top.amount">
                                          </div>
                                    </div>                                    
                                    </td>
                                </tr>
                                 <tr>
                                    <td class="text-xs"></td>
                                    <td>
                                    <div class="flex flex-row">
                                        
                                         <div class="w-1/2 mr-1">
                                          <label class="text-xs" for="ratio">Midbar Type</label>
                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2  rounded-l-lg leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="beam.rebar.mid.bartype">
                                        
                                        <option v-for="bar of bartypes" :key="bar.value" :value="bar.value">{{bar.text}}</option>
                                        </select>
                                        </div>
                                         <div class="w-1/2 ml-1">
                                          <label class="text-xs" for="ratio">Midbar Amt.</label>
                                        <input class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" type="text" placeholder="Amt." v-model="beam.rebar.mid.amount">
                                         </div>
                                    </div>                                    
                                    </td>
                                </tr>


                                <tr>
                                    <td class="text-xs"></td>
                                    <td>
                                    <div class="flex flex-row">
                                         <div class="w-1/2 mr-1">
                                          <label class="text-xs" for="ratio">Miainbar Type</label>
                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2  rounded-l-lg leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="beam.rebar.main.bartype">
                                        
                                        <option v-for="bar of bartypes" :key="bar.value" :value="bar.value">{{bar.text}}</option>
                                        </select>
                                        </div>
                                         <div class="w-1/2 ml-1">
                                          <label class="text-xs" for="ratio">Midbar Amt.</label>
                                          <input class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" placeholder="Amt." type="text" v-model="beam.rebar.main.amount">
                                        </div>
                                    </div>                                    
                                    </td>
                                </tr>

                               
                               
                                
                                 <tr>
                                    <td class="text-xs"></td>
                                    <td>
                                    <div class="flex flex-row">
                                        
                                         <div class="w-1/2 ml-1">
                                          <label class="text-xs" for="ratio">Stirrupbar Type</label>
                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-1 px-2 pr-2  rounded-l-lg leading-tight focus:outline-none focus:bg-white focus:border-gray-500" v-model="beam.rebar.stirrup.bartype">
                                        
                                        <option v-for="bar of bartypes" :key="bar.value" :value="bar.value">{{bar.text}}</option>
                                        </select>
                                        </div>
                                         <div class="w-1/2 ml-1">
                                          <label class="text-xs" for="ratio">Spacing</label>
                                          <span class="text-xs text-red-600 font-semibold mx-1">{{inputunit}}</span>
                                           <input type="number" class="px-2 py-1 w-full rounded-r-lg border hover:border-gray-500" v-model.number="beam.rebar.stirrup.spacing">
                                         
                                        
                                         </div>
                                    </div>                                    
                                    </td>
                                </tr>
                                 
                        </table>
                    </li>
                     <li class="p-2 m-1">
                         <div class="flex flex-row items-center">

                         <button class="bg-blue-700 text-white text-xs px-2 py-1 rounded-lg mr-2"  @click="runEstimate">Process Beam</button>
                         <button class="bg-blue-700 text-white text-xs px-2 py-1 rounded-lg" >Cancel</button>
                         </div>
                         </li>
                </ul>
            </div>
             <div class="w-1/2 p-5 bg-gray-700 text-yellow-200 text-xs rounded-lg mx-2">
             <preEstimate :project="project" />
            </div>
        </div>      
           
             <beamTable class="mt-5" :beam="beam" />
        
    </div>

    
</template>
<script>
import genny from '@/assets/utils/gennyJ'
import { mapGetters } from 'vuex'
import beamTable from '@/components/tables/beamTable.vue'
import preEstimate from '@/components/tables/preEstimate.vue'

export default {
    name: 'beam',
    components: {
        beamTable,
        preEstimate
    },
    props: {
        project_id: {
            type: String,
            required: true
        }
    },
    data(){
        return {
            
            message: null,
            fieldunit: null,
            inputunit: null,

            beam: {
                beamtype: null,
                category: null,
                tag: null,
                unit: null,
                length: null,
                depth: null,
                bredth: null,
                mix: {
                    ratio: null
                },
                rebar: {
                    cover: 30,
                    main: {
                        bartype: null,
                        lap: null,
                        lap_length: null,
                        length: null,
                        amount: null,
                        kg: null

                    },
                    mid: {
                        bartype: null,
                        lap: null,
                        lap_length: null,
                        length: null,
                        amount: 2,
                        kg: null

                    },
                    top: {
                        bartype: null,
                        lap: null,
                        lap_length: null,
                        length: null,
                        amount: 2,
                        kg: null

                    },
                    stirrup: {
                        spacing: 200,
                        span: false,
                        bartype: null,
                        length: null,
                        lap_length: 0,
                        amount: null,
                        kg: null
                    },
                    link: {
                        spacing: null,
                        span: true,
                        bartype: null,
                        length: null,
                        lap_length: 0,
                        amount: null,
                        kg: null
                    }
                }   
                        
            },
            project: {
                _id: null,
                estimate:{
                    id:null,
                    phase: null,
                    section: null,
                    items: [],
                    comment: null,
                    meta_data: {
                        created: Date.parse(new Date()),
                        author: null,
                    }
                    
                },
            },      
        }
          
    },
    
    mounted() {
        
        this.$store.dispatch('loadJobRates')
    },
    computed: {
        ...mapGetters({
            alpha_index: 'alpha_index',
            phases: 'currentEstimatePhases',
            sections: 'currentEstimateSections',
            jobrates: 'jobrates',
            beamtypes: 'beamtypes',
            beam_categories: 'beam_categories',
            bartypes: 'bar_types'

        }),

    

        processConcreteBeam() {
            var barlength = 9 // metric  
            const self = this
            if( self.beam.unit){
                 self.processUnits()
                 self.project._id =  self.project_id
            }
            // process reinforcements
            self.beam.rebar.top.length = self.beam.length
            self.beam.rebar.main.length = self.beam.length
            self.beam.rebar.mid.length = self.beam.length

            self.beam.rebar.top.lap = (self.beam.length / barlength).toFixed(2)
            self.beam.rebar.main.lap = (self.beam.length / barlength).toFixed(2)
            self.beam.rebar.mid.lap = (self.beam.length / barlength).toFixed(2)
            self.beam.rebar.stirrup.amount = (self.beam.length / self.beam.rebar.stirrup.spacing).toFixed(0)
            self.beam.rebar.stirrup.length = ((self.beam.bredth *2) +  (self.beam.depth * 2)).toFixed(2)
            
            const concrete = {
                category: "Concrete",
                mix: "manual",
                components: "aggregate",
                volume:  (self.beam.length *  self.beam.bredth *  self.beam.depth).toFixed(3), 
                ratio: self.beam.mix.ratio,
                rate: {
                    applicable: {},
                    rates: []
                }              
                
            }
            //apply concrete rate
            self.jobrates.forEach(item => {
                  //console.log(item)
                if(item.key.category == concrete.category){                   
                
                     concrete.rate.rates.push(item.key)
                }
                if(item.key.category == concrete.category && item.key.tags.includes( self.beam.beamtype) && item.key.tags.includes(concrete.mix) && item.key.tags.includes('ff')){
                     concrete.rate.applicable = item.key
                   
                }
                if(item.key.category == concrete.category && item.key.tags.includes( concrete.components) && item.key.tags.includes('man')){
                     concrete.rate.extra = item.key
                   
                }
            });

            concrete.item = {
                id: genny.genny('reinforced', 'concrete'),
                        title: "Mix and Place Concrete",
                        description: concrete.rate.applicable.description,
                        ado: null,
                        quantity: concrete.volume,
                        
            }
            concrete.subitem = {
                id: genny.genny('concrete', 'gang'),
                        title: "Batch Aggregates",
                        description: concrete.rate.extra.description,
                        ado: null,
                        quantity: concrete.volume,
                        
            }

           

            if(self.beam.beamtype == 'belt'){self.beam.girth = self.beam.depth * 2.0 }
            else {self.beam.girth = ( self.beam.depth * 2.0) +  self.beam.bredth }

            // Formwork Erection
            const formwork = {
                keword: "Erect",
                category: "Carpentry",
                length:  self.beam.length,
                width:  self.beam.bredth,
                area:  (self.beam.length * self.beam.girth).toFixed(2),
                rate: {
                    rates: [],
                    applicable: null
                }
            }
             //process formwork rate
              self.jobrates.forEach(item => {
                  //console.log(item)
                if(item.key.category == formwork.category){
                    
                
                     formwork.rate.rates.push(item.key)
                }
                if(item.key.category == formwork.category && item.key.tags.includes( self.beam.beamtype) && !item.key.description.includes('Make')){
                     formwork.rate.applicable = item.key
                   
                }
                if(item.key.category == formwork.category && item.key.tags.includes( self.beam.beamtype) && item.key.description.includes('Make')){
                     formwork.rate.extra = item.key
                   
                }
            });
            //console.log('FORMWORK', formwork)
            formwork.item = {
                id: genny.genny('form', 'erect'),
                        ado: null,
                        quantity: formwork.area,
                        
            }
            
            formwork.item.title = "Erect and Strike"           
            formwork.item.description = formwork.rate.applicable.description
            // Make Formwork
             
            formwork.makeitem = {
                id: genny.genny('form', 'make'),                        
                ado: null,
                quantity: formwork.area
                       
            }
            formwork.makeitem.title = "Make Formwork" 
            formwork.makeitem.description = formwork.rate.extra.description  


            if( self.beam.unit == "metric"){
                // REBAR
                //barlength = barlength / 1000
                self.beam.rebar.cover
                // CONCRETE
                concrete.item.rate = concrete.rate.applicable.rate.metric.production
                concrete.item.total = concrete.rate.applicable.rate.metric.total
                concrete.item.unit = "m3"
                concrete.item.cost = {
                    production: (concrete.item.rate * concrete.item.quantity).toFixed(2),
                    total: (concrete.item.total * concrete.item.quantity).toFixed(2)
                }
                // HANDLE AGGREGATES
                concrete.subitem.rate = concrete.rate.extra.rate.metric.production
                concrete.subitem.total = concrete.rate.extra.rate.metric.total
                concrete.subitem.unit = "m3"
                concrete.subitem.cost = {
                    production: (concrete.subitem.rate * concrete.subitem.quantity).toFixed(2),
                    total: (concrete.subitem.total * concrete.subitem.quantity).toFixed(2)
                }

                // ERECT FORMWORK
                formwork.item.rate = formwork.rate.applicable.rate.metric.production
                formwork.item.total = formwork.rate.applicable.rate.metric.total
                formwork.item.unit = "m2"
                formwork.item.cost = {
                    production: (formwork.item.rate * formwork.item.quantity).toFixed(2),
                    total: (formwork.item.total * formwork.item.quantity).toFixed(2)
                }
                // MAKE FORMWORK
                formwork.makeitem.rate = formwork.rate.extra.rate.metric.production
                formwork.makeitem.total = formwork.rate.extra.rate.metric.total
                formwork.makeitem.unit = "m2"
                formwork.makeitem.cost = {
                    production: (formwork. makeitem.rate * formwork.makeitem.quantity).toFixed(2),
                    total: (formwork.makeitem.total * formwork.makeitem.quantity).toFixed(2)
                }
            }else if( self.beam.unit == "imperial"){
                self.beam.rebar.cover / 312
                barlength = barlength / 3.125
                 // CONCRETE
                concrete.volume = concrete.volume / 27
                concrete.item.rate = concrete.rate.applicable.rate.imperial.production
                concrete.item.total = concrete.rate.applicable.rate.imperial.total
                concrete.item.unit = "Yd.Cu."
                concrete.item.cost = {
                    production: (concrete.item.rate * concrete.item.quantity).toFixed(2),
                    total: (concrete.item.total * concrete.item.quantity).toFixed(2)
                }
                // HANDLE AGGREGATES
                concrete.subitem.rate = concrete.rate.extra.rate.imperial.production
                concrete.subitem.total = concrete.rate.extra.rate.imperial.total
                concrete.subitem.unit = "Yd.Cu."
                concrete.subitem.cost = {
                    production: (concrete.subitem.rate * concrete.subitem.quantity).toFixed(2),
                    total: (concrete.subitem.total * concrete.subitem.quantity).toFixed(2)
                }
                // ERECT FORMWORK
                formwork.item.rate = formwork.rate.applicable.rate.imperial.production
                formwork.item.total = formwork.rate.applicable.rate.imperial.total
                formwork.item.unit = "Ft.Sq"
                formwork.item.cost = {
                    production: (formwork.item.rate * formwork.item.quantity).toFixed(2),
                    total: (formwork.item.total * formwork.item.quantity).toFixed(2)
                }
                 // MAKE FORMWORK
                formwork.makeitem.rate = formwork.rate.extra.rate.imperial.production
                formwork.makeitem.total = formwork.rate.extra.rate.imperial.total
                formwork.makeitem.unit = "Ft.Sq"
                formwork.makeitem.cost = {
                    production: (formwork. makeitem.rate * formwork.makeitem.quantity).toFixed(2),
                    total: (formwork.makeitem.total * formwork.makeitem.quantity).toFixed(2)
                }
            }
            //if (){}else{}
             self.project.estimate.id = genny.genny('concrete', 'beam')

             setTimeout(() => {
                formwork.item.item_no = self.alpha_index[self.project.estimate.items.length]
                self.project.estimate.items.push(formwork.item)
                 
             }, 1000);
            
             setTimeout(() => {
                formwork.makeitem.item_no = self.alpha_index[self.project.estimate.items.length]
                self.project.estimate.items.push(formwork.makeitem)
             }, 2000);

              setTimeout(() => {
                concrete.item.item_no = self.alpha_index[self.project.estimate.items.length]
                self.project.estimate.items.push(concrete.item)
             }, 3000);
            setTimeout(() => {
                concrete.subitem.item_no = self.alpha_index[self.project.estimate.items.length]
                self.project.estimate.items.push(concrete.subitem)
            }, 3500);
            concrete.rate.rates = []
            formwork.rate.rates = []

            self.beam.formwork = formwork
            self.beam.concrete = concrete

            return void 0

        }
    },
    methods: {
        // GETS
        async getRate() {
            //this.processEsections()
            /*
            this.jobrates.forEach(rate => {
                if(rate.key.category === this.wall.category){
                    this.wall.rate.rates.push(rate.key)
                }
                if(rate.key.category === this.wall.category && rate.key.tags.includes(this.wall.tag)){
                    this.wall.rate.applicable = rate.key
                   
                }
            });
            //console.log('APPLICABLE RATES ', this.wall.rates)
            */
            return void 0
        },
        
        async processUnits() { 

            if(this.beam.unit == 'metric'){
                this.inputunit = 'm'
                this.fieldunit = "m"
                this.beam.length  = this.beam.length /1000.0
                this.beam.bredth  =  this.beam.bredth / 1000.0
                this.beam.depth  =  this.beam.depth / 1000.0
                this.beam.rebar.stirrup.spacing =  this.beam.rebar.stirrup.spacing / 1000.0

            }else if(this.beam.unit == 'imperial'){
                this.inputunit = 'ft'
                this.fieldunit = "ft"
                this.beam.bredth  =  this.beam.bredth / 12.0
                this.beam.depth  =  this.beam.depth / 12.0
                this.beam.rebar.stirrup.spacing =  this.beam.rebar.stirrup.spacing / 12.0

            }
            this.message =  `Estimate Unit Set to ${this.beam.unit}`
        },
        async runEstimate() {
            this.processConcreteBeam
        },
        // SETS
        async setInputUnit(){
            if (this.beam.unit == "metric") {
                this.inputunit = "mm"
                this.fieldunit = "mm"
            }else if (this.beam.unit == "imperial") {
                this.inputunit = "inch"
                this.fieldunit = "ft"
            }
        }

        //RESETS
    }
    

}
</script>

