<template>
    <div class="home flex flex-row">
        <div class="card bg-gray-100 flex flex-col border border-gray-400 p-5 w-1/4 h-3/4 rounded-md overflow-y-auto">   
         <div class="pnl-left flex flex-col">

                <div class="w-full flex flex-wrap">

                    <div class="w-20 h-12 bg-teal-800 text-gray-200 text-xs text-center cursor-pointer m-2" @click=" manageConsole('foundation')">FOUNDATION</div>

                     <div>
                    <p class="w-20 h-12 bg-purple-800 text-gray-200 text-xs text-center m-2 cursor-pointer" @click=" manageConsole('wall')">WALL</p>
                    </div>

                    <div>
                        <p class="w-20 h-12 bg-blue-800 text-gray-200 text-xs text-center m-2 cursor-pointer" @click=" manageConsole('beam')">BEAM</p>
                    </div>
                    

                    <div>
                        <p class="w-20 h-12 bg-gray-800 text-gray-200 text-xs text-center cursor-pointer m-2" @click=" manageConsole('slab')">SLAB</p>
                    </div>

                    <div>
                        <p class="w-20 h-12 bg-green-800 text-gray-200 text-xs text-center cursor-pointer m-2" @click=" manageConsole('floor')">FLOOR</p>
                    </div>
                     <div>
                        <p class="w-20 h-12 bg-green-800 text-gray-200 text-xs text-center cursor-pointer m-2" @click=" manageConsole('roof')">ROOF</p>
                    </div>
                     <div v-if="!project.account">
                        <p class="w-20 h-20 bg-green-600 text-gray-100 text-xs text-center cursor-pointer m-2 rounded" @click="toggleUi('electrical')">Electrical</p>
                    </div>
                     

                </div>

          
            
        
            </div> 
        
           
             <rateTable />
            
        </div><!-- Left User Interface -->  
        <!-- Data Display --> 
        <div class="flex flex-col border border-gray-400 p-2 w-3/4 ml-1 mr-2 mb-2 rounded-md overflow-y-auto">
            <p class="bg-gray-200 py-2 px-2 text-gray-700 text-xs w-full rounded-lg shadow mb-5" v-if="ui.header"><button class="hover:bg-gray-400 px-full py-full">Add Item</button>|</p>
            <div id="emodal" class="overflow-y-auto" v-if="showing">
                <emodal :showing="showing" @close="showing=false">
                    <p>Estimate {{title}}</p> 
                    <foundation :project_id="project._id" v-if="console.foundation" />
                    <beam :project_id="project._id" v-if="console.beam" />
                    <column :project_id="project._id" v-if="console.column" />
                    <slab :project_id="project._id" v-if="console.slab" />
                    <floor :project_id="project._id" v-if="console.floor" />
                    <roof :project_id="project._id" v-if="console.roof" />
                    <wallEstimate v-if="console.wall"/> 

                            
                    <button class="bg-purple-700 text-white px-2 py-1 my-5 text-sm uppercase tracking-wide font-bold rounded-lg"
                        @click="showing=!showing"
                        >Close
                    </button>
                </emodal>           
            </div>

            <div v-if="showing== false">
                <div class="card bg-white border border-gray-300 rounded-lg p-2" v-for=" estimate of estimates" :key="estimate.id">

                    <!--tableView :title="chart_title" :tableData="estimate.items"/-->
                    <table class="bg-teal-300 w-full">
                        <caption>{{estimate.phase}}</caption>
                        <tr>
                            <th>Section</th>
                            <th>Items</th>
                        </tr>
                        <tr>
                            <td>{{estimate.section}}</td>
                            <td>
                                <tableView title="Items" :tableData="estimate.items" />
                            

                            </td>
                        </tr>
                    </table>
                 
                </div> 
            </div>  
                
            <div>
             
            </div>
        </div>
        {{project._id}}
        
       
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
//import { Location, Address } from '@/models/projectModel'
import rateTable from '@/components/project/rates/ratesTable.vue'
import tableView from '@/components/project/rates/TableView.vue'
import wallEstimate from '@/components/estimator/estimateWall.vue'
import foundation from '@/components/estimator/foundation.vue'
import beam from '@/components/estimator/beam.vue'
import column from '@/components/estimator/column.vue'
import slab from '@/components/estimator/slab.vue'
import floor from '@/components/estimator/floor.vue'
import roof from '@/components/estimator/roof.vue'
import Emodal from '@/components/ui/eModal.vue'  

export default {
    name: 'project-view',
    components: {
        rateTable,
        tableView,
        wallEstimate,
        Emodal,
        foundation,
        beam,
        column,
        slab,
        floor,
        roof
       
    },
  
    computed:{
        ...mapGetters({
            project: 'project',
            estimates: 'estimates',
            jobrates: 'jobrates',
           
            
               
        })
    },
    data(){
        return {
            title: null,
            showing: false,
            chart_title: 'Phase Items',
            address: null,
            id: this.$route.params.id.replace(':', ""),
            console: {
                foundation: false,
                wall: false,
                beam: false,
                slab: false,
                floor:false,
                roof: false
            },
            item: {
                
                iid: null,
                item_no: null,
                title: null,
                description: null,
                quantity: null,                      
                unit: null,
                rate: null,
                total: null,
            },
            estimate: {
               
                _id: null,
                item:{},
                phase: null,
                section: {},
                comment: null,
                metadata: {}

            },
            url: 'http://' + location.hostname +':5984/wkm_projects/',
            ui: {
                header: false,
                message: {
                    text: null,
                    css: null
                }

            },
            //location: null,
            
            wall: {
                category: null,
                tag: null,
                unit: null,
                length: null,
                height: null,
                thickness: null,
                area: null,
                rate:null
            },
          ru: null

        }
    },
    mounted() {
       
        

       setTimeout(() => {
           if (this.project){
            this.setMessage("Projects Loaded Sucessfully.", 'success')  
           }else {
            this.setMessage("Error projects unavailable!", 'error')  
              
           }

       }, 1000);
    },
    methods:{
        
        async manageConsole(signal) {
            this.showing = !this.showing
            this.console = {
                foundation: false,
                wall: false,
                beam: false,
                slab: false,
                floor:false,
                roof: false
            }
            this.console[signal] = true
            this.title = signal
        },
        async setMessage(message, signal){
            if(signal == 'success'){
                this.ui.message.text = message                
                this.ui.message.css = "text-sm text-green-500 p-5 m-5 border border-green-300"
            }else if (signal == 'error'){
                this.ui.message.text = message
                this.ui.message.css = "text-sm text-red-500 p-5 m-5 border border-red-300"
            }
            return void 0
        },
        async saveProject() {
        this.address.location = this.location
        this.$store.dispatch('getLocation', this.location)
       
        /*console.log(this.address.location.lat_x, this.address.location.lat_y )
        console.log('Address', this.address)
        console.log(Object.keys(this.address))*/
        }
    
    }

    
}
</script>


<style scoped>

#emodal {height: 500px;}
</style>
