<template>

    <div class="card bg-gray-100 flex flex-col border border-gray-400 p-5 mt-10 w-full h-3/4 rounded-md overflow-y-auto">  
    <div class="flex flex-row">
    <div class="w-1/2">

    <ul class="card bg-gray-200 p-5 rounded-lg">
        <li>
            
            <table class="text-sm text-teal-700 font-semibold">
                <caption>Construction Wall Estimate</caption>
                    <tr>
                        <th></th>
                        <th></th>
                    </tr>
                    <tr>
                        <td>Project Phase</td>
                        <td>
                         <div class="flex flex-row">
                                        <input type="text" class="px-2 ml-2 rounded-l-lg border hover:border-gray-500" v-model="wall.estimate.estimate.phase">
                                        <div>

                                        <select class="block w-full bg-gray-200 hover:bg-gray-400 border border-gray-200 text-gray-700 py-2 px-2 pr-6 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-walltype" v-model="wall.estimate.estimate.phase">
                                        <option v-for="p of phases" :key="p.value">{{p.text}}</option>
                                        </select>

                                       
                                        </div>

                                    </div>
                        </td>
                    </tr>
                     <tr>
                        <td>BQ Section</td>
                        <td><input type="text" v-model="wall.estimate.estimate.section"></td>
                    </tr>
                    <tr>
                        <td>Wall Type</td>
                        <td> <div class="relative" @focusout="setCategory">
                            <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-walltype" v-model="wall.walltype">
                            <option v-for="wt of wall_types.options" :key="wt.value">{{wt.text}}</option>
                            </select>
                            <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                            <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                            </div>
                            </div>
                            TAG {{wall.tag}}
                        </td>
                    </tr>
                    <tr>
                        <td>Category</td>
                        <td><input type="text" v-model="wall.category"></td>
                    </tr>
                    <tr class="px-3 mb-6 md:mb-0" @focusout="getRates()">
                            <td>Wall Tag</td>
                            <td>
                                <div class="relative" v-if="wall.category=='Masonry'">
                                    <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-walltag" v-model="wall.tag">
                                    <option v-for="wtt of masonry_options" :key="wtt.value">{{wtt.value}}</option>
                                    </select>
                                    <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                                    <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                                    </div>
                                </div>
                                <div class="relative" v-if="wall.category=='Drywall'">
                                    <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-2 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" type="text" v-model="wall.tag">
                                    <option v-for="wds of dws_options" :key="wds.value">{{wds.value}}</option>
                                    </select>
                                    <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                                    <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                                    </div>
                                </div>
                            </td>
                    </tr>
                     <tr>
                        <td>Unit</td>
                        <td>
                                                       
                            
                             <select type="text" v-model="wall.unit">
                                <option>metric</option>
                                <option>imperial</option>
                               
                    
                            </select>
                            
                            </td>
                    </tr>
                    <tr>
                        <td>Length</td>
                        <td><input type="number" v-model="wall.length" @focus="setWallThickness"></td>
                    </tr>
                    <tr>
                        <td>Height</td>
                        <td><input type="number" v-model="wall.height"></td>
                    </tr>
                     <tr>
                        <td>Thickness</td>
                        <td>
                            <select type="number" v-model="wall.thickness">
                                <option>75</option>
                                <option>100</option>
                                <option>150</option>
                                <option>200</option>
                                <option>250</option>
                    
                            </select>
                            {{wall.thickness}}
                        </td>
                    </tr>
            </table>
        </li>
           
                
            
                 <li class="p-2 m-1"><button class="bg-blue-700 text-white px-2 py-2 rounded-lg" @click="processWall">Process Wall</button></li>  
            </ul>
    </div>
     <div id="ap-rates" class="w-1/2 overflow-y-auto">
    
     <div class="card bg-gray-600 text-gray-300 text-xs rounded-lg p-2 m-2" v-if="wall.rate.rates.length > 0">
        <p>Applicable Rates Found ...</p>
        <ul class="card bg-gray-200 text-yellow-700 p-5 rounded-lg mb-5" v-for="rate of wall.rate.rates" :key="rate.id">
            <li>ID: {{rate.id}}</li>
            <li>Description: {{rate.description}}</li>
            <li>Rates:
                <ul v-for="val of rate.rate">
                    <li>{{val}}:
                      
                    </li>
                
                </ul></li>
        </ul>
       
    </div>
    <div v-else>
    <div class="card bg-gray-600 text-gray-300 text-xs rounded-lg p-2 m-2"  v-if="wall.tag">
        <p>Apply Your Rate Here...</p>
        Category: {{wall.category}}
        <ul class="card bg-gray-200 text-yellow-700 p-5 rounded-lg mb-5">
            <li>Title <input type="text" v-model="rate.title" @focusout="rate.category=wall.category"></li>
            <li>Description <input type="text" v-model="rate.description" @focusout="rate.tags.push(wall.tag)"></li>
            <li>    
                    
                        <table>
                            <caption>Imperial Rates</caption>
                            <tr>
                                <th></th>
                                <th></th>
                            </tr>
                            <tr>
                                <td>ADO</td>
                                <td><input class="w-3/4" type="number" v-model.number="rate.rate.imperial.ado"></td>
                            </tr>
                            <tr>
                                <td>Unt</td>
                                <td><input type="text" v-model="rate.rate.imperial.unit"></td>
                            </tr>
                            <tr>
                                <td>Production</td>
                                <td> <input type="number" v-model.number="rate.rate.imperial.production"></td>
                            </tr>
                            <tr>
                                <td>Total</td>
                                <td> <input type="number" v-model.number="rate.rate.imperial.total"></td>
                            </tr>
                        </table>
            </li>
                        
              
            
            <li>
               
                 <table>
                     <caption>Metric Rates</caption>
                            <tr>
                                <th></th>
                                <th></th>
                            </tr>
                            <tr>
                                <td>ADO</td>
                                <td><input class="w-3/4" type="number" v-model.number="rate.rate.metric.ado"></td>
                            </tr>
                            <tr>
                                <td>Unt</td>
                                <td><input type="text" v-model="rate.rate.metric.unit"></td>
                            </tr>
                            <tr>
                                <td>Production</td>
                                <td> <input type="number" v-model.number="rate.rate.metric.production"></td>
                            </tr>
                            <tr>
                                <td>Total</td>
                                <td> <input type="number" v-model.number="rate.rate.metric.total"></td>
                            </tr>
                        </table>
            </li>
            <li><button class="bg-green-500 text-white font-semibold py-1 px-1 rounded mt-5" @click="applyRate">Apply Rate</button></li>
           
        </ul>

    </div>
     </div>
    </div>
</div>
    <div class="card bg-gray-600 text-gray-300 text-xs rounded-lg p-2 m-2" v-if="wall.rate.applicable.description">
        <p>Applied Rate ...</p>
        {{wall.rate.applicable}}
    </div>
     <p class="bg-red-500 text-gray-200 text-xs">{{wall.estimate.estimate}}</p>
   
    <p v-if="temp.sections.length > 0">{{temp.sections}}</p>
    <P class="bg-teal-600 text-white p-4">BAR WEIGHTS {{barweights}}</p>
    </div>

</template>
<script>
import genny from '@/assets/utils/gennyJ'
import { mapGetters } from 'vuex'



export default {
    name: 'wall-estimate',
    data() {
        return {  
            phases: [
                 {value: "prelim", text:"Preliminaries"},
                  {value: "sub", text:"Substructure"},


            ],
            rate: {
                _id: null,
                category: null,
                title:null,
                description: null,
                rate:{
                    imperial: {},
                    metric: {}
                },
                tags: []
            },
            rebar: {
                    vbar_spacing: 400,
                    vbar_type: "m12",
                    vbar_height: null,

                    hbar_spacing: 600,
                    hbar_type: "m10" 
            },
            frame: {
                stud_spacing: 400,
                stud_type: "25x50",
                stud_height: null,

                track_spacing: 600,
                track_type: "25x50" 

            },
           
            wall_types: {
                selected: "Wall Type",
                options: [                    
                    {value: "cmu", text:"BlockWall"},
                    {value: "dws", text:"Drywall"}
                   
                    ]
                
            },
            wall: {
                walltype: "Wall Type",
                category: null,
                rate: {
                    applicable: {
                        rate:{
                            imperial: {
                                ado:null,
                                production:null,
                                total:null
                            },
                            metric: {
                                ado:null,
                                production:null,
                                total:null
                            }
                        }
                    },
                    rates: [],
                },
                tag: null,
                unit: "metric",
                length: null,
                height: null,
                thickness: null,               
                estimate: {
                    
                    estimate: {
                        id: null,
                        phase: null, // if phase and section append items  
                        section: null, // if not section append estimate
                        items: [],
                    },
                    item: {
                        id: null,
                        item_no: null,
                        title: null,
                        description: null,
                        ado: null,
                        quantity: null,
                        unit: null,
                        rate: null,
                        total: null
                    },
                    //required: true
                }
            },
            temp: {
                phases: new Set,
                sections: new Set
            }
        }

    },
   
    computed:{
        ...mapGetters({
        jobrates: 'jobrates',
        estimates: 'estimates',
        cephases: 'currentEstimatePhases',
        cesections:'currentEstimateSections',

        barweights: 'barweights',
        masonry_options: 'masonry_options',
        dws_options: 'dws_options'
        
        }),
       
        processCMU() {
            //console.log(this.cephases)

            
            const m2yd = 10.7639,
                iado = this.wall.rate.applicable.rate.imperial.ado,
                openings = null,
                area = (this.wall.length * this.wall.height) - openings,
                iarea = area * m2yd,

                wall =  this.wall.rate.applicable.rate;
                wall.imperial.quantity = iarea // convert m2 to yd2
                wall.imperial.output = iarea / iado // Daily output one worker
                wall.imperial.cost = {
                    production: wall.imperial.production * iarea,
                    final: wall.imperial.total * iarea
                }
                wall.metric.quantity = area
                wall.metric.ado = iado / m2yd
                wall.metric.output = area / wall.metric.ado
                wall.metric.cost = {
                    production: wall.metric.production * area,
                    final: wall.metric.total * area
                }
                wall.concrete = {

                }  
                const item = {
                    item: {
                        id: genny.genny('Wall', 'item'),//wall.title, wall.tag),
                        item_no: null,
                        title: this.wall.tag + ' BlockWall',
                        description: this.wall.rate.applicable.description,
                        output: wall.metric.output,
                        quantity: wall.metric.quantity,
                        unit: wall.metric.unit,
                        rate: wall.metric.production,
                        total: wall.metric.quantity * wall.metric.production
                    },                
                    phase: this.wall.estimate.estimate.phase,
                    section: this.wall.estimate.estimate.section                    

                }
            this.wall.estimate.estimate.items.push(item.item)
            this.$store.dispatch('addEstimateItem', item)
           
            /*if(this.cephases.has(this.wall.estimate.estimate.phase) && this.cesections.has(this.wall.estimate.estimate.section)) { 
                console.log('Estimate ITEM', item)
                console.log('phase sections', this.cephases, this.cesections)
                this.wall.estimate.estimate.items.push(item)

            }*/
           
            
            //console.log(wall)
            
            return wall 
        },
        processWallbars () {
            const rates = []
            const vert =  {
                    bartype:  this.barweights[this.wall.rebar.vbar_type],
                    length: this.wall.rebar.vbar_height, 
                    amount: this.wall.length / this.wall.rebar.vbar_spacing,
                    rate: null
                },
                horiz = {
                    bartype:  this.barweights[this.wall.rebar.hbar_type],
                    length: this.wall.length, 
                    amount: this.wall.height / this.wall.rebar.hbar_spacing,
                    rate: null

                };
                this.jobrates.forEach(rate => {
                    if(rate.key.category === 'Steelwork'){
                        if(rate.key.description.includes(vert.bartype.d) || rate.key.description.includes(vert.bartype.bytpe)){
                            vert.rate = rate.key.rate
                            rates.push(rate.key)
                        }else if (rate.key.description.includes(horiz.bartype.d) || rate.key.description.includes(horiz.bartype.bytpe)){
                            horiz.rate = rate.key.rate
                            rates.push(rate.key)
                        }
                    }
                })
                //console.log(rates)
                /* VERTICAL BARS */
                vert.quantity = vert.length * vert.amount // length in meter
                vert.kg = vert.quantity  * vert.bartype.kgm // kg mass
                vert.bars = vert.quantity / 9.0 // Lengths of bars 
                const vertItem = {
                    item: {
                        id: genny.genny('Re', 'Bar'),//wall.title, wall.tag),
                        item_no: null,//this.processItemSection,
                        title: `${vert.bartype.d }mm ${vert.bartype.btype}`,
                        description: `${vert.bartype.d }mm ${vert.bartype.btype} (Vertical)`,
                        //output: wall.metric.output,
                        quantity: vert.kg,
                        unit: vert.rate.metric.unit,
                        rate: vert.rate.metric.production,
                        total: vert.kg * vert.rate.metric.production
                    },
                    phase: this.wall.estimate.estimate.phase,
                    section: this.wall.estimate.estimate.section     
                }
                //console.log('VERTICAL BAR', vert)//Item)
                //console.log('VERTICAL BAR ITEM', vertItem)
                setTimeout(() => {
                    this.wall.estimate.estimate.items.push(vertItem.item)
                    this.$store.dispatch('addEstimateItem', vertItem)
                }, 1000);

                /* HORIZONTAL BARS */
                horiz.quantity = horiz.length * horiz.amount // length in meter
                horiz.kg = horiz.quantity  * horiz.bartype.kgm // kg mass
                horiz.bars = horiz.quantity / 9.0 // Lengths of bars 

                const horizItem = {
                    item:{
                        id: genny.genny('Re', 'Bar'),//wall.title, wall.tag),
                        item_no: null,//this.processItemSection,
                        title: `${horiz.bartype.d }mm ${horiz.bartype.btype}`,
                        description: `${horiz.bartype.d }mm ${horiz.bartype.btype} (Horizontal)`,
                        /*output: wall.metric.output,*/
                        quantity: horiz.kg,
                        unit: horiz.rate.metric.unit,
                        rate: horiz.rate.metric.production,
                        total: horiz.kg * horiz.rate.metric.production
                    },
                    phase: this.wall.estimate.estimate.phase,
                    section: this.wall.estimate.estimate.section   
                }
                setTimeout(() => {
                    this.wall.estimate.estimate.items.push(horizItem.item)
                    this.$store.dispatch('addEstimateItem', horizItem)
                }, 1500);
                //console.log('HORIZONTAL BAR ITEM', horiz)//Item)
                 //console.log('HORIZONTAL BAR ITEM', horizItem)
            return {
                vertical: vert,
                horizontal: horiz
            }
            
        },
        processDrywall() {
            const length = this.wall.length,
                height = this.wall.height,
                thickness = this.wall.thickness,
                m2yd = 10.7639,
                iado = this.wall.rate.applicable.rate.imperial.ado,
                openings = null,
                area = (length * height) - openings,
                iarea = area * m2yd,

                wall =  this.wall.rate.applicable.rate;
                wall.imperial.quantity = iarea // convert m2 to yd2
                wall.imperial.output = iarea / iado // Daily output one worker
                wall.imperial.cost = {
                    production: wall.imperial.production * iarea,
                    final: wall.imperial.total * iarea
                }
                wall.metric.quantity = area
                wall.metric.ado = wall.imperial.iado / m2yd
                wall.metric.output = area / wall.metric.ado
                wall.metric.cost = {
                    production: wall.metric.production * area,
                    final: wall.metric.total * area
                }
                             
            
            return wall 
        },
        processItemSection(){           
            const abv = ['a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','z']
            if(this.cephases.has(this.wall.estimate.estimate.phase)) { 
                this.estimates.forEach(estimate => {                
                    estimate.items.forEach(item => {
                        
                            abv.shift()
                        
                    })
                    
                })
                const nextItem = abv.shift()
                //this.temp.sections.add(nextItem)
                
                return nextItem
            }
            else {
                const nextItem = abv.shift()
                //this.temp.sections.add(nextItem)
                return nextItem
            }
        }
    },
    methods: {
       
        async setupWall() {
            if(this.wall.unit === "metric"){
                this.wall.length = this.wall.length/1000     // convert measurement from mm to m 
                this.wall.height = this.wall.height/1000

                if(this.wall.category === "Masonry"){
                    this.wall.rebar = this.rebar
                        this.wall.rebar.vbar_spacing = this.wall.rebar.vbar_spacing/1000
                        this.wall.rebar.vbar_height = this.wall.height + 0.3 // extra over for bend and lap
                        this.wall.rebar.hbar_spacing = this.wall.rebar.hbar_spacing/100
                }
                else if(this.wall.category === "Drywall"){
                        this.wall.frame = this.frame
                        this.wall.frame.stud_spacing = this.wall.frame.stud_spacing/1000
                        this.wall.rebar.stud_height = this.wall.height 
                        this.wall.rebar.track_spacing = this.wall.frame.track_spacing/1000
                }
                
            }
            else if(this.wall.unit === "imperial"){
               
                if(this.wall.category === "Masonry"){
                    this.wall.rebar = this.rebar
                        this.wall.rebar.vbar_spacing = this.wall.rebar.vbar_spacing/12
                        this.wall.rebar.vbar_height = this.wall.height + 1 // extra over for bend and lap
                        this.wall.rebar.hbar_spacing = this.wall.rebar.hbar_spacing/12

                }
                else if(this.wall.category === "Drywall"){
                        this.wall.frame = this.frame
                        this.wall.frame.stud_spacing = this.wall.frame.stud_spacing/12
                        this.wall.rebar.stud_height = this.wall.height 
                        this.wall.rebar.track_spacing = this.wall.frame.track_spacing/12
                }
                
            }
           
        },
        async setWallThickness(){
        
            if(this.wall.tag == "75mm"){
                
                this.wall.thickness = 75
            }
            else if(this.wall.tag == "100mm"){
                this.wall.thickness = 100
            }
            else if(this.wall.tag == "150mm"){
                this.wall.thickness = 150
            }
            else if(this.wall.tag == "200mm"){
                this.wall.thickness = 200
            }
            else if(this.wall.tag == "250mm"){
                this.wall.thickness = 250
            }
        },
        
        async getRates() {
            //this.processEsections()
            
            this.jobrates.forEach(rate => {
                if(rate.key.category === this.wall.category){
                    this.wall.rate.rates.push(rate.key)
                }
                if(rate.key.category === this.wall.category && rate.key.tags.includes(this.wall.tag)){
                    this.wall.rate.applicable = rate.key
                   
                }
            });
            //console.log('APPLICABLE RATES ', this.wall.rates)
            return void 0
        },
        
        async setCategory() {
            if(this.wall.walltype == "BlockWall"){
                this.wall.category = "Masonry"
                
            }
            else if(this.wall.walltype == "Drywall"){
                this.wall.category = "Drywall"
                
            }

        },
        async processWall(){
            this.setupWall()
            if(this.wall.category === "Masonry"){
                this.wall.estimate.estimate.id = genny.genny('concrete', 'masonry')
                this.processCMU
                this.processWallbars
                
                //this.$store.dispatch('updateEstimate', this.wall.estimate.estimate)
               
                
            }
            else if(this.wall.category === "Drywall"){
                 this.wall.estimate.estimate.id = genny.genny('dry', 'wall')
                 wall: this.processDrywall
                
            }

        },
        async applyRate() {
              if(this.wall.category === 'Drywall'){
                this.wall_types.dws_options.forEach(option => {
                    if(option.value == this.wall.tag){
                        this.rate.specification = option
                    }
                })
            }
            else if(this.wall.category == 'Masonry'){
                this.wall_types.masonry_options.forEach(option => {
                    if(option.value == this.wall.tag){
                        this.rate.specification = option
                    }
                })
            }
            if(!this.rate.title){
                this.rate.title = prompt('Item Needs a Title')
            }
            else if(!this.rate.description){
                this.rate.description = this.rate.specification.text
            }
            else if(!this.rate.category) {
                this.rate.category = this.wall.category
            }
            else if(!this.rate.rate.imperial.production){
                this.rate.rate.imperial.production = prompt('You need to enter an Imperial production Rate ')
                this.rate.rate.imperial.total = Math.round(this.rate.rate.impreial.production)
            }
            else if(!this.rate.rate.metric.production){
                this.rate.rate.metric.production = prompt('You need to enter a Matric production Rate ')
                this.rate.rate.metric.total = Math.round(this.rate.rate.metric.production)
            }
            this.rate._id = genny.genny(this.rate.title, this.rate.description)
            this.rate.jobclass = 'job'
           /*
            for (let [key, value] of Object.entries(this.rate.rate.imperial)) {
                console.log(`${key}: ${value}`);
                if(value == null){
                    this.rate.rate.imperial[key] = prompt(`You need to enter a value for ${key}`)
                }
                }
            for (let [key, value] of Object.entries(this.rate.rate.metric)) {
                console.log(`${key}: ${value}`);
                if(value == null){
                    this.rate.rate.metric[key] = prompt(`You need to enter a value for ${key}`)
                }
                }
            */        
           
            this.wall.rate.applicable = this.rate
            this.$store.dispatch('addJobRate', this.rate)

        },
        // RESETS
        async resetItem(){
            this.item = {
                        id: null,
                        item_no: null,
                        title: null,
                        description: null,
                        ado: null,
                        quantity: null,
                        unit: null,
                        rate: null,
                        total: null
                    }

        }
    },

    
}
</script>
<style scoped>
#ap-rates {height: 400px;}
</style>

