<template>
    <div>
       
        <div id="table-view"></div>
    </div>
</template>

<script>

import Tabulator from 'tabulator-tables';

export default {
    name: "table-view",
    props: {
        title: {
            type: String,
            required: true
        },
        tableData: {
            type: Array,
            required: true
        }

    },
    data() {
        return {
          
            ui: {
                show: {
                    index_view: true,
                    table_view: true
                }
            }

        }
    },
    mounted(){
       
      this.loadDataTable()
       
    },
    
    
    methods:{
        async loadDataTable() {
            const self = this
            var table = new Tabulator("#table-view", {
               tooltips: true,
               
                //height: "500px",

                data: self.tableData,
                layout: "fitDataFill",
                responsiveLayout:"hide",
                pagination: "local",
                paginationSize: 10,
                //paginationElement:paginationElement,
                paginationButtonCount: 3,
                paginationSizeSelector:true,
                columns:[
                    {title: "Item", field:"item_no", width:80},
                    {title: "Title", field:"title", width:120},
                    {title: "Description", field:"description", width:200},
                    {title: "ADO", field:"ado", width:80},                    
                    {title: "Amount", field:"quantity", width:100},                    
                    {title: "Unit", field:"unit", width:80},
                    {title: "Rate", field:"rate", width:120, formatter:"money", formatterParams:{ symbol:"$"}} 
                ],
               
                rowClick: function(e, row){
                    
                    //self.$store.dispatch('getProject', row.getData().id)
                    //self.$router.push('/project/:' + row.getData().id )
                    alert("Clone this Rate ?..")
                }
            });

        }
    }
    
}
</script>


<style src="@/assets/css/table.css" />